.pagination{
  margin-top: 2.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__items{
    display: flex;
    justify-content: center;
    gap: 2em;
  }
  &__item{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: -.01em;
    &._active{
      pointer-events: none;
      color: $primary;
      font-weight: 600;
    }
  }
  &__btn{
    width: 3.125em;
    height: 3.125em;
    @include bgImgSize(100%);
  }
  ._prev{
    background-image: url(../img/prev.svg);
  }
  ._next{
    background-image: url(../img/next.svg);
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 2em;
  }
}


@media screen and (max-width: 576px){
  .pagination{
    margin-top: 1.5em;
    &__items{
      gap: 1.5em;
    }
    &__item{
      font-size: 1em;
    }
    &__btn{
      width: 2.125em;
      height: 2.125em;
    }
  }
}
