.test-main{
  padding: 2em 0 3.75em;
  &__inner{
    background: #F0F0F0;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 3em),calc(100% - 1.375em) 100%,0 100%,0 0);
    padding: 2.5em 2em;
    min-height: 31.25em;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 50%;
    right: 2em;
    transform: translateY(-50%);
    max-height: 100%;
    max-width: 56.3125em;
  }
  &__content{
    width: 32em;
  }
  &__desc{
    margin-top: .75em;
    .m-subtitle{
      font-size: 2.25em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 10.69em;
    display: flex;
  }
  &__btn{
    width: 24.5415em;
    height: 4em;
    text-transform: uppercase;
    span{
      font-size: 1.58em;
    }
  }
  &__info{
    display: none;
  }
}

.search{
  margin-top: 5em;
  display: flex;
  .fg{
    flex-grow: 1;
    input{
      font-size: 1em;
      padding-left: 3.75em;
      height: 3.75em;
    }
    img{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 2em;
      width: 1.5em;
    }
  }
  &__btn{
    height: 3.75em;
    width: 15.125em;
    margin-left: 1em;
    flex-shrink: 0;
    span{
      font-size: 1em;
    }
  }
}

.knowledge{
  display: grid;
  grid-template-columns: 23.5em calc(100% - 28.5em);
  gap: 5em;
  margin-top: 2.5em;
  &__tab{
    height: 4.625em;
    padding: 0 2em;
    display: flex;
    align-items: center;
    font-weight: 600;
    cursor: pointer;
    &._active{
      pointer-events: none;
      background: $white2;
    }
    span{
      letter-spacing: -.01em;
    }
  }
  &__title{
    display: none;
  }
  .qa-card__btn{
    width: 2em;
    height: 2em;
    background-image: url(../img/plus2.svg),url(../img/minus2.svg);
    @include bgImgSize(100%);
    background-size: 100% auto, 0 auto;
  }
  .qa-card._active .qa-card__btn{
    background-size: 0 auto, 100% auto;
  }
}


@media screen and (max-width: 992px){
  .test-main{
    padding: 1em 0 1.5em;
    &__inner{
      padding: 1em 1em 1.5em;
      min-height: unset;
      margin-top: 1.5em;
    }
    &__img{
      position: static;
      transform: unset;
      display: block;
      width: 100%;
      max-width: 38em;
      margin: -.94em auto 0;
    }
    &__content{
      width: 100%;
      position: relative;
      z-index: 2;
    }
    &__desc{
      .m-subtitle{
        font-size: 1.5em;
      }
    }
    &__nav{
      display: none;
    }
    &__info{
      margin-top: 3em;
      display: block;
      text-align: center;
      &-desc{
        width: 24em;
        margin: .75em auto 0;
      }
    }
  }

  .search{
    margin-top: 2em;
    .fg{
      input{
        padding-left: 1.25em;
      }
      img{
        display: none;
      }
    }
    &__btn{
      margin-left: .5em;
    }
  }

  .knowledge{
    grid-template-columns: 100%;
    &__col{
      display: none;
    }
    &__title{
      display: block;
      text-align: center;
    }
    &__items{
      margin-top: 1.125em;
      @include grid100gap(1.125em);
    }
    .tab-content{
      display: block;
      margin-bottom: 1.875em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    .qa-card{
      border: none;
      &__show{
        padding: 0;
        min-height: unset;
      }
      &__btn{
        width: 1.5em;
        height: 1.5em;
        margin-left: .81em;
      }
      &__hidden{
        margin-top: .5em;
      }
      &__text{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .test-main{
    &__inner{
      clip-path: unset;
    }
    &__img{
      margin-top: 5em;
    }
    &__desc{
      margin-top: .5em;
    }
    &__info{
      margin-top: 3.75em;
      &-desc{
        display: none;
      }
      .h1{
        font-size: 1.625em;
      }
    }
  }

  .search{
    margin-top: 1.375em;
    .fg{
      input{
        padding: 0 .67em;
        font-size: .75em;
        height: 2.67em;
      }
    }
    &__btn{
      margin-left: .33em;
      width: 7.83em;
      height: 2.67em;
      font-size: .75em;
    }
  }
}
