@import "../vars.scss";
.breadcrumbs {
    margin-bottom: 2.75em;
    color: #666;
    font-weight: 300;
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: .625em;
            &:before {
                content: '/';
                position: absolute;
                top: -.1875em;
                right: 100%;
                margin-right: .125em;
                font-size: 1.25em;
                line-height: 120%;
            }
        }
        &:last-child{
            pointer-events: none;
            color: $primary;
            &:before{
                color: #666;
            }
        }
    }
    a,span{
        font-size: 1.25em;
        line-height: 120%;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        margin-bottom: .5em;
        ul{
            font-size: .7em;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        margin-bottom: .25em;
        ul{
            font-size: .6em;
        }
    }
}
