.events-slider2{
  margin-top: 2.5em;
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.event-card2{
  position: relative;
  &:hover{
    &:before{
      opacity: 1;
    }
  }
  &:before{
    content: '';
    @include centerAbsolute;
    width: calc(100% + 2.5em);
    height: calc(100% + 2.5em);
    background: #EEECE0;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  .event-card__col,
  .event-card__col2{
    position: relative;
    z-index: 2;
  }
  .event-card__top{
    gap: .5em;
  }
  .event-card__desc,
  .event-card__date{
    font-size: 1em;
  }
  .event-card__title{
    font-size: 1.5em;
  }
  .event-card__bottom{
    padding-top: 1em;
  }
  .event-card__link{
    font-size: 1em;
  }
}

._pag-num{
  .swiper-pagination{
    &-bullet{
      width: unset;
      height: unset;
      background: transparent !important;
      font-size: 1.5em;
      letter-spacing: -.01em;
      &:before{
        display: none;
      }
      &-active{
        color: $primary;
        font-weight: 600;
      }
    }
  }
}

.about-main2{
  color: #EEECE0;
  background-color: #EEECE0;
  padding-bottom: 2em;
  &__title{
    font-size: 7.5em;
    line-height: 120%;
    letter-spacing: -.01em;
    font-weight: 600;
    text-transform: uppercase;
  }
  .about-main__logo{
    margin-top: .5em;
  }
  ._view2{
    display: none;
  }
  .about-main__img{
    clip-path: polygon(3% 0, 100% 0, 100% 85%, 97% 100%, 0 100%, 0 15%);
  }
}

.events-page{
  background-color: #FEFEF6;
  flex-grow: 1;
  padding: 2em 0 4em;
  &__grid{
    margin-top: 2.5em;
    row-gap: 4.25em;
  }
  &__grid2{
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 4.25em 2.5em;
  }
}


@media screen and (max-width: 992px){
  .events-slider2{
    margin-top: 2em;
    .swiper-slide{
      width: 50%;
    }
  }

  .event-card2{
    grid-template-columns: 100%;
    gap: 1em;
    &:before{
      display: none;
    }
    .event-card__top{
      gap: .5em;
    }
    .event-card__title{
      font-size: 1.25em;
    }
  }

  .about-main2{
    color: $black;
    &__title{
      font-size: 3em;
    }
    .about-main__logo{
      margin-top: 1em;
    }
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
    .about-main__img{
      clip-path: unset;
      margin-top: 3.5em;
    }
  }

  .events-page{
    padding: 1em 0 3em;
    &__grid{
      margin-top: 1.5em;
      row-gap: 2.5em;
    }
    &__grid2{
      grid-template-columns: repeat(2,calc(50% - 1.75em / 2));
      gap: 2.5em 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .events-slider2{
    margin-top: 1.5em;
    .swiper-slide{
      width: 100%;
    }
  }

  .event-card2{
    gap: .75em;
    .event-card__col2{
      order: 2;
      position: static;
    }
    .event-card__top{
      gap: .35em;
    }
    .event-card__title{
      font-size: .875em;
      font-weight: 600;
    }
    .event-card__desc{
      display: block;
      font-size: .75em;
    }
  }

  .about-main2{
    &__title{
      font-size: 2.5em;
      color: $primary;
    }
    .about-main__img{
      margin-top: 1.5em;
    }
  }

  .events-page{
    padding: .5em 0 2em;
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 2em .5em;
    }
    &__grid2{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 2em .5em;
    }
    .event-card__img{
      padding-bottom: 76.7%;
    }
    .event-card__desc{
      @include truncate(4);
    }
    .story-card__title{
      font-size: 1.25em;
    }
  }
}
