.main-bottom{
  margin-top: 3.75em;
  text-align: center;
  &__desc{
    width: 69em;
    max-width: 100%;
    margin: 1.75em auto 0;
  }
  &__nav{
    margin-top: 3.75em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    height: 2.875em;
    font-weight: 600;
    padding: 0 1.5em;
    span{
      font-size: 1em;
    }
  }
}

.character{
  position: relative;
  &__view{
    z-index: 3;
    position: absolute;
    top: 2.125em;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 2.5em;
    &-btn{
      font-weight: 600;
      text-transform: uppercase;
      min-width: 19em;
      span{
        font-size: 1em;
        letter-spacing: unset;
      }
      &:first-child{
        clip-path: polygon(.5em 0, 100% 0, 100% 100%, 0 100%, 0 1.5em);
      }
      &._active{
        background-color: $primary;
        color: $white;
      }
    }
  }
  &__colors{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 5.25em;
    padding: 1em 2.25em;
    border-radius: 0.25em .25em 0 0;
    background: rgba(0, 0, 0, 0.00);
    border: 1px solid $white;
    border-bottom: none;
    z-index: 3;
    display: flex;
    align-items: flex-start;
    gap: 1.25em;
  }
  .tab-content._active{
    //display: none;
    //&._active-color{
    //  display: block;
    //}
  }
}

.color-tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.character-color{
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgba($white,.5);
  cursor: pointer;
  &._active{
    pointer-events: none;
    color: $white;
    .character-color__top{
      padding: 0;
    }
  }
  &__top{
    transition: all .3s;
    height: 2.875em;
    margin-bottom: .5em;
    padding: .375em 0;
  }
  &__bg{
    width: 4.75em;
    height: 100%;
    border-radius: 0.125em;
    background: #3A5F2D;
  }
  &__title{
    font-size: .75em;
  }
}

.character-slider{
  margin-top: 4em;
  position: relative;
  .slider__nav{
    min-height: unset;
    height: 2.875em;
  }
}

.character-card{
  &__img{
    @include imgCover(0);
    height: 41.25em;
  }
}


@media screen and (max-width: 992px){
  .main-bottom{
    .h1{
      width: 11em;
      margin: 0 auto;
    }
    &__desc{
      margin: 2em 0 0;
      text-align: left;
      width: 40.5em;
      .body-l{
        font-size: 1.5em;
      }
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .character{
    &__view{
      position: static;
      gap: 1.5em;
      margin-bottom: 1.5em;
    }
    &__colors{
      position: static;
      transform: unset;
      margin-top: 1.5em;
      padding: 0;
      border-radius: 0;
      border: none;
      justify-content: center;
    }
  }

  .character-color{
    &__top{
      height: 2.125em;
      margin-bottom: 1em;
      padding:  0;
    }
  }

  .character-slider{
    margin-top: 2em;
    .slider__nav{
      height: 2em;
      margin-top: 1.5em;
    }
  }

  .character-card{
    &__img{
      height: 31.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-bottom{
    &__desc{
      margin-top: 1.5em;
      .body-l{
        font-size: 1em;
      }
    }
    &__btn{
      width: 100%;
    }
  }

  .character{
    &__view{
      gap: .5em;
      margin-bottom: 1em;
      &-btn{
        min-width: unset;
        width: calc(50% - .25em);
        height: 2.5em;
        padding: 0;
        span{
          font-size: .75em;
        }
      }
    }
  }

  .character-slider{
    margin-top: 1.5em;
  }

  .character-card{
    &__img{
      height: 18.125em;
    }
  }

  .character-color{
    &__top{
      height: 2em;
      margin-bottom: .25em;
    }
    &__bg{
      width: 4em;
    }
  }
}
