.req2{
  @media screen and (min-width: 993px){
    .req-contacts__textarea{
      grid-row: 2/4;
    }
    .req-contacts__wishes{
      grid-column: 2/3;
      grid-row: 1/2;
    }
  }
}

.req-contacts{
  &__wishes{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
  &__hint{
    color: #2F3629;
    margin-top: 2em;
    text-align: center;
  }
}

.test-drive{
  padding: 2em 0 3.75em;
  .req-dealer{
    margin-top: 3.75em;
    .h5{
      font-size: 1.5em;
    }
  }
}

.dealers{
  margin-top: 1.5em;
  background: #EEECE0;
  box-shadow: 2px 4px 15px 0 rgba(164, 164, 164, 0.10);
  display: grid;
  grid-template-columns: 36.75em calc(100% - 36.75em);
  &__col{
    padding: 1.5em .81em 1.5em 2em;
  }
  &__map{
    height: 100%;
    min-height: 30em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__items{
    height: 28em;
    padding-right: 5em;
    overflow-y: auto;
    @include scroll;
    @include grid100gap(1.25em);
    &._open{
      .dealer-item{
        display: none;
        &._active{
          display: block;
          .dealer-item__show{
            display: none;
          }
          .dealer-item__hidden{
            display: block;
          }
        }
      }
    }
  }
}

.dealer-item{
  &__show{
    @include grid100gap(.5em);
    padding-bottom: 1.25em;
    border-bottom: 1px solid #D9D6C7;
    font-weight: 600;
    cursor: pointer;
  }
  &__hidden{
    display: none;
  }
  &__title{
    font-size: 1.5em;
    line-height: 121%;
    color: $primary;
    font-weight: 600;
  }
  &__street{
    font-size: 1.125em;
    line-height: 130%;
  }
  &__props{
    @include grid100gap(1.5em);
    margin-top: 2.25em;
  }
  &__prop{
    @include grid100gap(.5em);
    &-title{
      font-size: 1.25em;
      line-height: 130%;
      color: $primary;
    }
  }
  &__back{
    display: flex;
    align-items: center;
    font-weight: 600;
    margin-bottom: 4em;
    &:hover{
      color: $primary;
    }
    img{
      width: 1.5em;
      margin-right: .25em;
    }
  }
}


@media screen and (max-width: 992px){
  .test-drive{
    padding: .5em 0 3em;
    .req-dealer{
      margin-top: 3em;
    }
  }

  .dealers{
    margin-top: 1.5em;
    padding: 1em;
    grid-template-columns: 100%;
    gap: 1.875em;
    &__col{
      padding: .5em 0 0;
    }
    &__map{
      min-height: 15em;
    }
    &__items{
      height: unset;
      padding-right: 0;
    }
  }

  .dealer-item{
    &__title{
      font-size: 1.25em;
    }
    &__street{
      font-size: 1em;
    }
    &__props{
      margin-top: 1.5em;
    }
    &__prop{
      &-title{
        font-size: 1em;
      }
    }
    &__back{
      margin-bottom: 2.25em;
      img{
        width: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .test-drive{
    .h1{
      max-width: 70%;
    }
  }
}
