.blog-slider2{
  @media screen and (min-width: 993px){
    margin-top: 5em;
    .swiper-slide{
      width: 25%;
    }
  }
}

.no-wrap{
  white-space: nowrap;
}

.req{
  background: #EEECE0;
  padding: 6.25em 0;
  &__inner{
    background: #FEFDF6;
    padding: 2.5em 8.75em 5em;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 5.5em),calc(100% - 2em) 100%,0 100%,0 0);
  }
}

.req-form{
  margin-top: 1.75em;
  &__colors{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
    .req-equip__item{
      width: 3.75em;
      flex-grow: 0;
      label{
        border: none;
      }
    }
  }
}

.req-equip{
  .fg{
    display: none;
  }
  &__items{
    margin-top: 1.125em;
    display: flex;
    flex-wrap: wrap;
    gap: 1.1875em;
  }
  &__item{
    flex-grow: 1;
    label{
      display: flex;
      align-items: center;
      min-height: 2.875em;
      padding: .125em 1.125em;
      background: #EEECE0;
      transition: all .3s;
    }
    input:checked~label{
      box-shadow: 0 1px 2px rgba($primary,.3),0 0 0 .125em $primary;
    }
  }
}

.req-contacts{
  margin-top: 2em;
  &__fields{
    margin-top: 1.125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
    grid-auto-flow: dense;
  }
  &__textarea{
    grid-column: 2/3;
    grid-row: 1/3;
  }
  &__nav{
    margin-top: 2.875em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    height: 3.75em;
    width: 42.5em;
    max-width: 100%;
  }
}

.req-dealer{
  margin-top: 1.875em;
  &__grid{
    margin-top: 1.125em;
    display: flex;
  }
  .fg{
    flex-grow: 1;
  }
  &__btn{
    flex-shrink: 0;
    min-width: 15em;
    margin-left: 1em;
    min-height: 100%;
    height: 3.75em;
    img{
      width: 1.625em;
      margin-right: .75em ;
    }
  }
}


@media screen and (max-width: 992px){
  .req{
    padding: 3.5em 0;
    &__inner{
      padding: 2.5em 1.5em;
      clip-path: unset;
    }
    .h1{
      max-width: 10em;
      margin: 0 auto;
    }
  }
  .req-form{
    margin-top: 1.5em;
    &__colors{
      display: none;
    }
  }

  .req-contacts{
    margin-top: 1em;
    &__fields{
      grid-template-columns: 100%;
      gap: 1em;
      margin-top: .5em;
    }
    &__textarea{
      grid-column: unset;
      grid-row: unset;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      height: 3em;
      span{
        font-size: 1em;
      }
    }
  }

  .req-dealer{
    margin-top: 1em;
    &__grid{
      margin-top: .5em;
    }
    &__btn{
      display: none;
    }
  }

  .req-equip{
    .fg{
      display: block;
      margin-top: .5em;
    }
    &__items{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .req{
    &__inner{
      padding: 2.5em .5em;
    }
  }
}
