.auto{
  background: #1B1D19;
  color: $white;
}

.auto-info{
  margin-top: 8em;
  @include grid100gap(6.25em);
  &__row{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 1.5em 2em;
  }
  &__img{
    width: 100%;
    display: block;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 5em),calc(100% - 2em) 100%,0 100%,0 0);
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__props{
    margin-top: 2.1875em;
    @include grid100gap(.375em);
  }
  @media screen and (min-width: 993px){
    .auto-info__row:nth-child(even){
      .auto-info__col2{
        order: -1;
      }
    }
  }
}

.prop-item{
  padding-left: 2.25em;
  border-left: .25em solid #FDFFD1;
  transition: border-color .3s;
  &._open{
    border-color: $primary;
    .prop-item__top{
      &:after{
        background-size: 0 auto, 100% auto;
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    &:after{
      content: '';
      width: 2.5em;
      height: 2.5em;
      @include bgImgSize(100%);
      background-image: url(../img/plus.svg),url(../img/minus.svg);
      background-size: 100% auto, 0 auto;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 200%;
    letter-spacing: -.01em;
    text-transform: uppercase;
  }
  &__bottom{
    display: none;
    margin-top: .125em;
  }
  &__desc{
    font-size: .9375em;
    line-height: 173%;
    letter-spacing: -.01em;
    color: $white3;
  }
}

.mechanic{
  margin-top: 25em;
  &__img{
    @include centerAbsolute;
    width: 15em;
    z-index: 2;
  }
}

.mechanic-slider{
  margin-top: 4.25em;
  position: relative;
  @media screen and (min-width: 577px){
    .swiper-container{
      width: 100%;
    }
    .swiper-wrapper{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      transform: unset !important;
    }
    .slider__nav{
      display: none;
    }
    .swiper-slide{
      padding: 0;
      &:nth-child(2),
      &:nth-child(4){
        .mechanic-card{
          padding-right: 2.125em;
          padding-left: 8.75em;
        }
        .mechanic-card__num{
          left: 2.125em;
          right: unset;
        }
      }
      &:nth-child(3),
      &:nth-child(4){
        .mechanic-card__num{
          top: unset;
          bottom: 2.125em;
        }
      }
    }
  }
}

.mechanic-card{
  height: 100%;
  border: 1px solid #555;
  background: #353931;
  padding: 2.125em 7.25em 1.5em 8em;
  position: relative;
  &__num{
    position: absolute;
    top: 2.125em;
    right: 2.125em;
    background: #FEFDF6;
    width: 2.875em;
    height: 2.875em;
    @include center;
    color: $black;
    span{
      font-size: 1.5em;
      letter-spacing: -.01em;
    }
  }
  .h3{
    line-height: 167%;
  }
  &__desc{
    margin-top: .375em;
    color: $white3;
  }
}


@media screen and (max-width: 992px){
  .auto-info{
    margin-top: 3.5em;
    gap: 2em;
    &__row{
      grid-template-columns: 100%;
    }
    &__img{
      clip-path: unset;
      object-fit: cover;
      height: 25em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__props{
      margin-top: 1.875em;
    }
  }

  .prop-item{
    font-size: .67em;
  }

  .mechanic{
    margin-top: 25em;
    .h1{
      width: 100%;
    }
    &__img{
      width: 7.75em;
    }
  }

  .mechanic-slider{
    margin-top: 1.5em;
    @media screen and (min-width: 577px){
      .swiper-wrapper{
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1em;
      }
      .swiper-slide{
        padding: 0;
        &:nth-child(4),
        &:nth-child(2){
          .mechanic-card__num{
            left: unset;
            right: .75em;
          }
        }
        &:nth-child(3),
        &:nth-child(4){
          .mechanic-card__num{
            top: unset;
            bottom: 1em;
          }
        }
      }
    }
  }

  .mechanic-card{
    border: none;
    padding: 2em 1em 5.5em !important;
    &__num{
      top: 2em;
      right: .75em;
      background: #FDFFD1;
    }
    &__top{
      min-height: 2.875em;
      padding-right: 3em;
      display: flex;
      align-items: center;
    }
    .h3{
      color: #FDFFD1;
      text-transform: unset;
      font-weight: 600;
      line-height: 120%;
    }
    &__desc{
      margin-top: .75em;
      .body-l{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .auto-info{
    margin-top: 2em;
    gap: 1em;
    &__img{
      height: unset;
    }
    &__desc{
      margin-top: .75em;
    }
    &__props{
      margin-top: 1.5em;
      gap: .25em;
    }
  }

  .mechanic{
    margin-top: 18em;
    &__img{
      top: unset;
      bottom: 1.5em;
      transform: translateX(-50%);
    }
  }

  .mechanic-slider{
    .slider__nav{
      min-height: unset;
      margin-top: 5.375em;
    }
  }

  .mechanic-card{
    padding: 1em .75em 6.5em !important;
    &__num{
      right: .75em;
      top: 1em;
    }
  }
}
