.community{
  padding: 4.5em 0 5.75em;
  background: #2F3629;
  color: $white;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__img{
    @include imgCover(69.6%);
    min-height: 100%;
    ._view2{
      display: none;
    }
  }
  &__desc{
    margin-top: 4.5em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__link{
    min-width: 23.5em;
    height: 3em;
    span{
      font-size: 1em;
    }
  }
}

.stories{
  padding: 4em 0;
  background-color: #EEECE0;
}

.stories-slider{
  margin-top: 7em;
  .swiper-slide{
    width: 25%;
  }
}

.story-card{
  &__img{
    @include imgCover(133%);
  }
  &__content{
    @include grid100gap(.75em);
    margin-top: 1.69em;
  }
  &__title{
    font-size: 1.5em;
  }
  &__city{
    margin-top: -.5em;
  }
  &__desc{
    line-height: 120%;
  }
}

.events{
  padding: 3.125em 0 2.75em;
  background-color: #EEECE0;
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__link{
    height: 3em;
    min-width: 23.5em;
    span{
      font-size: 1em;
    }
  }
}

.events-slider{
  margin-top: 2.5em;
}

.event-card{
  &__col2{
    display: flex;
    flex-direction: column;
  }
  &__img{
    @include imgCover(77.3%);
  }
  &__top{
    @include grid100gap(1em);
  }
  &__date{
    font-size: 1.4375em;
    line-height: 120%;
    letter-spacing: -.01em;
  }
  &__title{
    font-size: 2.5em;
    line-height: 105%;
    font-weight: 700;
    letter-spacing: -.01em;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: -.01em;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1em;
    display: flex;
  }
  &__link{
    font-size: 1.5em;
    letter-spacing: -.01em;
    line-height: 120%;
    text-decoration: underline;
    color: #353931;
  }
}


@media screen and (max-width: 992px){
  .community{
    padding: 3.5em 0;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .h1{
      font-size: 2em;
      text-transform: unset;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      display: flex;
      margin-top: .75em;
    }
    &__link{
      min-width: unset;
      width: 100%;
    }
  }

  .stories{
    padding: 3.5em 0;
  }

  .stories-slider{
    margin-top: 2em;
    .swiper-slide{
      width: 50%;
    }
  }

  .events{
    padding: 2em 0 3.5em;
  }

  .events-slider{
    margin-top: 2em;
  }

  .event-card{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: .75em 1em;
    &__img{
      padding-bottom: 88.4%;
    }
    &__top{
      gap: .5em;
    }
    &__desc,
    &__date{
      font-size: 1em;
    }
    &__title{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 576px){
  .community{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      order: -1;
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view1{
        display: block;
      }
    }
    .h1{
      text-transform: uppercase;
      font-size: 1.875em;
    }
    &__desc{
      margin-top: .75em;
    }
    &__nav{
      margin-top: .5em;
    }
  }

  .stories-slider{
    margin-top: 1.5em;
    .swiper-slide{
      width: 100%;
    }
  }

  .story-card{
    &__content{
      margin-top: .75em;
      gap: .25em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__city{
      font-size: .75em;
      margin-top: -.25em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .events{
    &__nav{
      margin-top: 1.5em;
    }
    &__link{
      min-width: unset;
      width: 100%;
    }
  }

  .events-slider{
    margin-top: 1.5em;
  }

  .event-card{
    grid-template-columns: 100%;
    position: relative;
    padding-bottom: 1.6875em;
    &__col2{
      order: -1;
    }
    &__img{
      padding-bottom: 62%;
    }
    &__top{
      gap: .5em;
    }
    &__desc{
      display: none;
    }
    &__date{
      font-size: .75em;
    }
    &__title{
      font-size: .875em;
      font-weight: 500;
    }
    &__bottom{
      padding: 0;
      position: absolute;
      left: 0;
      bottom: 0;
    }
    &__link{
      font-size: 1em;
    }
  }
}
