.faq{
  padding: 3.75em 0 6.25em;
  &__items{
    margin-top: 3.44em;
  }
}

.qa-card{
  border-top: 1px solid #242422;
  &:last-child{
    border-bottom: 1px solid #242422;
  }
  &._active{
    .qa-card__btn img{
      transform: rotate(-180deg);
    }
  }
  &__show{
    padding: 1.25em 2em 1.25em 1.5em;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 5.375em;
  }
  &__title{
    text-transform: unset;
  }
  &__btn{
    margin-left: .25em;
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    svg,
    img{
      transition: all .3s;
      width: 100%;
    }
  }
  &__hidden{
    padding: 0 1.5em 1.25em;
    display: none;
  }
  &__text{
    font-size: 1em;
    line-height: 162%;
    letter-spacing: -.01em;
  }
  &__props{
    @include grid100gap(1.5em);
  }
  &__prop{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: .5em 1em;
    flex-wrap: wrap;
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1.25em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    padding-right: 1.25em;
    height: auto;
  }
  &__link{
    @include centerAbsolute;
    min-width: 14.6875em;
  }
  &__nav{
    position: relative;
    margin-top: 2.375em;
    display: flex;
    align-items: center;
    min-height: 2.875em;
    .swiper-nav{
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    .swiper-button{
      position: static;
      margin: 0;
    }
  }
  .swiper-pagination{
    @include centerAbsolute;
    max-width: calc(100% - 7em);
  }
}

.swiper-pagination{
  @include center;
  &-bullet{
    background-color: $white3;
    height: .25em;
    width: 7.5em;
    opacity: 1;
    border-radius: 0;
    margin: 0 .625em !important;
    position: relative;
    &:before{
      content: '';
      @include centerAbsolute;
      width: 100%;
      height: 600%;
    }
    &-active{
      background-color: $primary !important;
    }
  }
  &._black{
    .swiper-pagination-bullet{
      background-color: #353931;
    }
  }
}

.swiper-button{
  width: 3.125em;
  @include center;
  &:hover{
    opacity: .8;
  }
  img{
    width: 100%;
  }
}

.main-blog{
  padding: 6.25em 0;
}

.blog-slider{
  margin-top: 4.25em;
  .swiper-slide{
    width: calc(100% / 3);
  }
}

.blog-card {
  &--video{
    .blog-card__img{
      &:after{
        content: '';
        @include centerAbsolute;
        @include bgImgSize(100%);
        background-image: url(../img/play.svg);
        width: 4.5625em;
        height: 4.5625em;
      }
    }
  }
  &__img{
    @include imgCover(56.19%);
  }
  &__content{
    margin-top: 1.25em;
    min-height: 5em;
    @include grid100gap(.75em);
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding: 2.5em 0 3.5em;
    &__items{
      margin-top: 1.25em;
    }
  }

  .qa-card{
    &__show{
      padding: 1em 0;
    }
    &__hidden{
      padding: 0 0 1em;
      margin-top: -.5em;
    }
    &__text{
      font-size: .875em;
    }
  }

  .swiper-pagination{
    &-bullet{
      height: .125em;
      width: 3.5em;
      margin: 0 .5em !important;
    }
  }

  .swiper-button{
    width: 2.125em;
  }

  .slider{
    .swiper-container{
      width: calc(100% + 1em);
    }
    .swiper-slide{
      padding-right: 1em;
    }
    &__link{
      height: 2em;
      border-width: 1px;
      span{
        font-size: .75em;
      }
    }
    &__nav{
      margin-top: 1.5em;
      min-height: 2em;
    }
    .swiper-pagination{
      max-width: calc(100% - 5em);
    }
  }

  .main-blog{
    padding: 2.5em 0;
  }

  .blog-slider2,
  .blog-slider{
    margin-top: 1.5em;
    .swiper-slide{
      width: 50%;
    }
  }

  .blog-card {
    &__img{
      padding-bottom: 77%;
    }
    &__content{
      margin-top: 1em;
      min-height: unset;
      @include grid100gap(.75em);
    }
    .h3{
      font-size: 1.25em;
      text-transform: unset;
      font-weight: 400;
    }
    &__desc,
    &__date{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .blog-slider2,
  .blog-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
