.videos-collection{
  background-image: url(../img/videos-collection-bg.webp);
  background-size: cover;
  background-position: center top;
}

.video-slider{
  margin-top: 4.25em;
  .swiper-slide{
    width: 25%;
  }
}

.video-card{
  display: block;
  &__img{
    @include imgCover(177.4%);
    &:after{
      content: '';
      @include centerAbsolute;
      @include bgImgSize(100%);
      background-image: url(../img/play.svg);
      width: 8.5625em;
      height: 8.5625em;
    }
  }
}

.advantages{
  background-color: #2F3629;
  background-image: url(../img/mechanic.webp);
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: left 0 bottom 30.75em;
  color: #FEFDF6;
  &__video{
    margin-top: 2.81em;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 6.3em),calc(100% - 2.4em) 100%,0 100%,0 0);
    video{
      width: 100%;
      display: block;
    }
  }
}

.advantage-item{
  &__top{
    display: flex;
    margin-bottom: .25em;
  }
  &__icon{
    width: 2.875em;
    flex-shrink: 0;
    margin-right: 1.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 4.375em;
    font-weight: 600;
    text-transform: uppercase;
    margin-top: -.1875em;
  }
  &__measure{
    font-size: 1.5em;
    font-weight: 700;
    margin-left: .375em;
  }
}

.challenges{
  margin-top: 6.25em;
}

.challenge-card{
  &__img{
    padding-bottom: 67%;
  }
}

.challenges-slider{
  margin-top: 4.25em;
  .swiper-slide{
    width: calc(100% / 3);
  }
}


@media screen and (max-width: 992px){
  .videos-collection{
    .h1{
      width: 7em;
      margin: 0 auto;
    }
  }

  .video-slider{
    margin-top: 2em;
    .swiper-container{
      width: calc(100% + .75em);
    }
    .swiper-slide{
      padding-right: .75em;
    }
  }

  .video-card{
    &__img{
      &:after{
        width: 3.94em;
        height: 3.94em;
      }
    }
  }

  .advantages{
    overflow: hidden;
    background-size: 150%;
    background-position: center bottom 49.75em;
    .h1{
      width: 9em;
      margin: 0 auto;
    }
    .info-block__desc{
      margin-top: 2em;
      .text18{
        font-size: 2em;
        line-height: 110%;
        font-weight: 700;
        text-align: left;
      }
    }
    &__video{
      margin-top: 2em;
      clip-path: unset;
    }
    &__items{
      display: flex;
      justify-content: space-between;
      margin-top: 1.5em;
    }
  }

  .advantage-item{
    &__top{
      margin-bottom: .5em;
    }
    &__icon{
      width: 2em;
      margin-right: .5em;
    }
    &__title{
      font-size: 2.5em;
    }
    &__measure{
      font-size: .75em;
      font-weight: 300;
      margin-left: 0;
    }
    .body-l{
      font-weight: 300;
      font-size: .75em;
      line-height: 120%;
    }
  }

  .challenges{
    margin-top: 3.5em;
    .h1{
      width: 11em;
    }
  }

  .challenges-slider{
    margin-top: 2em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 24.5em;
    }
  }

  .challenge-card{
    &__content{
      gap: .5em;
    }
    .h3{
      font-size: 1.25em;
      font-weight: 400;
    }
    &__desc{
      font-size: 1em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 576px){
  .video-slider{
    margin-top: 1.5em;
    .swiper-slide{
      width: 100%;
    }
  }

  .video-card{
    &__img{
      &:after{
        width: 8.5em;
        height: 8.5em;
      }
    }
  }

  .advantages{
    //background-size: 100% auto;
    background-position: center bottom 35em;
    .info-block__desc{
      margin-top: 1.5em;
      .text18{
        font-size: 1.25em;
        font-weight: 400;
      }
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1.5em 1em;
      margin-top: 2em;
    }
  }

  .challenges-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
