.models{
  margin-top: 2em;
  &__grid{
    margin-top: 2em;
  }
}

.model-card{
  padding: 1.5em;
  background: #EEECE0;
  clip-path: polygon(0 0,100% 0,100% calc(100% - 3em),calc(100% - 1.375em) 100%,0 100%,0 0);
  transition: all .3s;
  &:hover{
    cursor: pointer;
    background: #2F3629;
    color: $white;
  }
  &__img{
    @include imgCover(68.9%);
    margin-bottom: 1.5em;
  }
  &__title{
    font-size: 2em;
  }
}

.article{
  padding: 2em 0;
  background: #EEECE0;
  &__inner{
    color: #2F3629;
    padding: 4.5em 6em;
    background: #FEFEF6;
  }
  &__top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1em);
    max-width: 64em;
  }
  &__title{
    font-size: 2.5em;
  }
  &__date{
    padding: .85em 0 0 1em;
    white-space: nowrap;
  }
  .text{
    margin-top: 2.5em;
  }
}

.other-news{
  padding: 1.75em 0 5em;
  .h1{
    font-size: 2em;
  }
  .events-slider2{
    margin-top: 2em;
  }
  .event-card{
    &__title{
      font-weight: 600;
    }
    &__img{
      padding-bottom: 56%;
    }
  }
}

.our-tg{
  padding: 4em 0 1.75em;
  &__inner{
    padding: 5.5em;
    background: $primary;
    box-shadow: 2px 4px 15px 0 rgba(164, 164, 164, 0.10);
    color: $white;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .h2{
    text-transform: unset;
    font-weight: 600;
    letter-spacing: -.01em;
    font-size: 2.5em;
  }
  &__desc{
    margin-top: 4em;
  }
  &__link{
    margin-top: 2em;
    height: 3em;
    min-width: 23.5em;
    font-weight: 600;
    background-color: #EEECE0;
  }
}


@media screen and (max-width: 992px){
  .models{
    margin-top: 1.5em;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .1875em));
      gap: .375em;
    }
  }

  .model-card{
    padding: .5em .25em;
    &__img{
      margin-bottom: .5em;
    }
    &__title{
      font-size: 1.5em;
    }
  }

  .article{
    padding: .5em 0 1em;
    &__inner{
      padding: 1em 1em 3.25em;
    }
    &__title{
      font-size: .875em;
      text-transform: unset;
    }
    &__info{
      gap: .25em;
    }
    .text{
      margin-top: .75em;
    }
  }

  .other-news{
    padding: 1em 0 2em;
    .h1{
      display: none;
    }
    .events-slider2{
      margin-top: 0;
    }
    .slider__nav{
      margin-top: 1em;
    }
    .event-card{
      &__img{
        padding-bottom: 66.2%;
      }
    }
  }

  .our-tg{
    padding: 1.5em 0 1em;
    &__inner{
      padding: 3.5em;
    }
    .h2{
      max-width: 11.75em;
      font-size: 2em;
      line-height: 120%;
    }
    &__desc{
      margin-top: 2.5em;
    }
    &__link{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .model-card{
    clip-path: polygon(0 0,100% 0,100% calc(100% - 1.3125em),calc(100% - .3em) 100%,0 100%,0 0);
    &__title{
      font-size: 1em;
    }
  }

  .article{
    &__top{
      flex-direction: column;
    }
    &__info{
      max-width: 18.75em;
    }
    &__date{
      padding: 0 0 1em;
      order: -1;
    }
  }

  .our-tg{
    padding-top: 1em;
    &__inner{
      padding: 3em 1em;
    }
    .h2{
      font-size: 1.5em;
    }
    &__desc{
      max-width: 17em;
    }
    &__link{
      margin-top: 1em;
      min-width: unset;
      width: 100%;
    }
  }
}
