.equip{
  background: #2F3629;
  color: $white;
  overflow: hidden;
  .qa-card{
    border-color: $white;
    path{
      stroke: $white;
    }
  }
  @media screen and (min-width: 993px){
    .tab-content{
      display: none;
      &._active{
        display: none;
      }
    }
  }
}

.equip-table{
  margin-top: 4.25em;
  border-bottom: 1px solid $white;
  table{
    width: 100%;
    border-spacing: 0;
  }
  th{
    background: #4C5744;
    .h3{
      text-transform: unset;
    }
  }
  th,td{
    padding-left: 1.25em;
    text-align: center;
    &:first-child{
      width: 36em;
      padding-left: 1.5em;
      text-align: left;
    }
  }
  td{
    height: 3.5em;
    padding-top: .25em;
    padding-bottom: .25em;
  }
  tbody{
    tr{
      &:nth-child(even){
        td{
          background: #4C5744;
        }
      }
    }
  }
  .equip-item{
    padding: .125em 0;
  }
}

.equip-item{
  text-align: center;
  @include grid100gap(.125em);
  &__title{
    font-size: .8125em;
    line-height: 184%;
  }
  &__desc{
    font-size: 1.4375em;
    line-height: 121%;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.equip-tabs{
  display: none;
}


@media screen and (max-width: 992px){
  .equip{
    .h1{
      width: 9em;
      margin: 0 auto;
    }
    .tab-content{
      margin-top: 2em;
    }
  }

  .equip-table{
    display: none;
  }

  .equip-tabs{
    display: block;
    margin-top: 2em;
    .slider__nav{
      margin: 0 0 1em;
    }
    .swiper-slide{
      width: 12em;
    }
    ._tab{
      opacity: .46;
      cursor: pointer;
      transition: all .3s;
      &._active{
        opacity: 1;
      }
    }
  }

  .equip-item{
    gap: .5em;
    &__title{
      font-size: .75em;
      font-weight: 300;
      line-height: 120%;
    }
    &__desc{
      font-size: 1.25em;
      font-weight: 400;
    }
  }
}


@media screen and (max-width: 576px){
  .equip-tabs{
    margin-top: 1.5em;
  }
}
