.header{
  padding: 1.125em 0;
  color: $white;
  background-color: $primary;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 17.5em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.nav{
  display: flex;
  gap: 2.875em;
  &__link{
    text-transform: uppercase;
    font-size: 1.125em;
    font-weight: 600;
    line-height: 120%;
    &:hover{
      color: $white3;
    }
  }
}

.menu__nav,
.menu__top,
.burger{
  display: none;
}

.header-action{
  display: flex;
  &__phone{
    width: 2.875em;
    padding: 0;
    img{
      width: 1.575em;
    }
  }
  &__btn{
    font-weight: 600;
    min-width: 12em;
    margin-left: 1em;
  }
}

.page-nav{
  background: #2F3629;
  position: sticky;
  top: 0;
  z-index: 10;
  &__btn{
    min-width: 15.875em;
    text-transform: uppercase;
  }
  &__links{
    display: flex;
    gap: 3.75em;
  }
  &__link{
    padding: 1.125em 0;
    display: flex;
    align-items: center;
    position: relative;
    color: #F1F3C7;
    &:hover{
      color: $white;
    }
    span{
      font-size: 1.125em;
      line-height: 140%;
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: .375em;
      transition: all .3s;
      opacity: 0;
      background-color: $primary;
    }
    &._active{
      &:before{
        opacity: 1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 6.5em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__top{
      background: #1B1D19;
      position: fixed;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1.5em 1em;
      z-index: 4;
      width: 20em;
    }
    &__scroll{
      position: fixed;
      z-index: 3;
      width: 20em;
      height: $vh;
      background: #1B1D19;
      padding: 5em 1em 2em;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.50) 0%, rgba(116, 102, 89, 0.00) 100%);
      z-index: 2;
    }
    &__nav{
      margin-top: auto;
      padding-top: 1em;
      display: flex;
    }
    &__btn{
      font-weight: 600;
      text-transform: uppercase;
      width: 15.875em;
      height: 3em;
      span{
        font-size: .75em;
      }
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    gap: 1.5em;
    &__link{
      text-transform: unset;
      font-size: 1.5em;
      font-weight: 400;
      line-height: 110%;
    }
  }

  .menu__close,
  .burger{
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    margin-right: .3125em;
    img{
      width: 100%;
    }
  }

  .header-action{
    &__phone{
      display: none;
    }
    &__btn{
      font-size: .75em;
    }
  }

  .page-nav{
    display: none;
  }
}


