.action{
  position: fixed;
  bottom: 3.5em;
  right: 0;
  border-radius: 1em;
  background: rgba(47, 54, 41, 0.20);
  padding: 1.875em 1.6em;
  z-index: 5;
  gap: 2.5em;
  display: flex;
  flex-direction: column;
  &__btn{
    width: 4em;
    height: 4em;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    svg{
      width: 2.25em;
    }
  }
}

.footer{
  padding: 3.5em 0;
  background: #2F3629;
  color: $white;
  &__inner{
    row-gap: 2.5em;
  }
  a:hover{
    color: $white;
  }
}

.footer-nav{
  &__title{
    font-size: 1.5em;
    font-weight: 600;
    line-height: 150%;
  }
  &__hidden{
    margin-top: 1.5em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
  &__links{
    color: #FDFFD1;
    @include grid100gap(1em);
    a{
      letter-spacing: -.01em;
      font-size: 1em;
      line-height: 150%;
    }
  }
}

.footer-bottom{
  grid-column: span 4;
}

.footer-address{
  &__items{
    margin-top: .75em;
    @include grid100gap(.5em);
  }
  &__item{
    font-size: 1em;
    line-height: 150%;
  }
}

.footer-contacts{
  grid-column: span 2;
  &__phones{
    margin-top: .75em;
    @include grid100gap(.75em);
  }
  &__phone{
    font-size: 2.125em;
    display: inline-block;
    margin-right: auto;
  }
}

.footer-socials{
  &__items{
    display: flex;
    gap: 1em;
    margin-top: .875em;
    margin-bottom: 1.125em;
  }
  &__item{
    width: 2.5em;
    height: 2.5em;
    @include center;
    img{
      width: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-10%);
      }
    }
  }
  &__site{
    color: $white3;
  }
}

.footer-links{
  grid-column: 1/-1;
  font-weight: 300;
  &__item{
    font-size: .875em;
    line-height: 140%;
    display: inline-block;
    margin-right: auto;
  }
}


@media screen and (max-width: 992px){
  .action{
    background: rgba(47, 54, 41, 0.60);
    backdrop-filter: blur(2px);
    padding: 1em .75em;
    gap: 1em;
    right: .5em;
    &__btn{
      width: 3em;
      height: 3em;
      background-color: #FEFDF6;
      svg{
        width: 1.4em;
      }
      path{
        fill: $primary;
      }
    }
  }

  .footer{
    &__inner{
      grid-template-columns: repeat(3,calc(100% / 3 - 6.25em / 3));
      gap: 3.75em 3.125em;
    }
  }

  .footer-nav{
    &__title{
      font-size: 1em;
    }
    &__hidden{
      margin-top: 1.25em;
    }
    &__links{
      color: #FDFFD1;
      gap: .75em;
      a{
        font-size: .875em;
      }
    }
  }

  .footer-bottom{
    grid-column: span 2;
    gap: 1.875em 3.125em;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
  }

  .footer-address{
    &__item{
      font-size: .875em;
    }
  }

  .footer-contacts{
    grid-column: span 1;
    &__phone{
      font-size: 1.5em;
    }
  }

  .footer-links{
    display: flex;
    justify-content: space-between;
    &__item{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .footer-nav{
    &._open{
      .footer-nav__show:after{
        transform: rotate(-180deg);
      }
    }
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:after{
        content: '';
        background-image: url(../img/arrow-bottom2.svg);
        @include bgImgSize(100%);
        margin-left: 1em;
        width: 1.375em;
        height: 1.375em;
        transition: all .3s;
      }
    }
    &__title{
      font-size: 1.125em;
    }
    &__hidden{
      margin-top: .75em;
      display: none;
    }
    &__links{
      a{
        font-size: 1em;
      }
    }
  }

  .footer-bottom{
    grid-column: span 1;
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .footer-links{
    flex-direction: column;
    align-items: flex-start;
    gap: 5em;
    &__item{
      max-width: 14em;
      &--c{
        margin: 0 auto;
        order: 2;
        max-width: unset;
      }
    }
  }
}
