.adventurer{
  background: #2F3629;
  padding-top: 3.75em;
  color: $white;
  overflow: hidden;
  &__desc{
    width: 69em;
    text-align: center;
    max-width: 100%;
    margin: 0 auto;
    color: $white3;
    p{
      line-height: inherit;
      letter-spacing: inherit;
    }
  }
  .advantages__items{
    margin-top: 4.5em;
  }
  .challenges-slider{
    position: relative;
    &__nav{
      @include center;
      gap: 2.19em;
      width: 100%;
    }
    @media screen and (min-width: 993px){
      .swiper-container{
        width: 100%;
      }
      .swiper-slide{
        padding: 0;
        width: 100%;
      }
      .swiper-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
        gap: 1.25em;
      }
    }
  }
  .challenge-card__img{
    padding-bottom: 77.4%;
  }
  @media screen and (min-width: 993px){
    .slider__nav{
      min-height: unset;
    }
    .swiper-pagination{
      display: none;
    }
  }
}

.main{
  padding: 3em 0 3.75em;
  background: #1B1D19;
  color: $white;
}

.main-top{
  padding: 4.375em 5em;
  position: relative;
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 6.3em),calc(100% - 2.4em) 100%,0 100%,0 0);
    overflow: hidden;
    &:after{
      background: linear-gradient(180deg, rgba(0, 0, 0, 0.50) 0%, rgba(116, 102, 89, 0.00) 100%);
    }
    video{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__name{
    font-size: 7.5em;
    line-height: 117%;
    font-weight: 600;
    letter-spacing: -.01em;
  }
  &__price{
    @include grid100gap(.5em);
    margin-top: 7em;
    &-title{

    }
    &-num{
      font-size: 3.75em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    font-weight: 600;
    font-size: 1.5em;
  }
}

.adventurer-slider{
  position: relative;
  margin-top: 3.75em;
  .swiper-button{
    top: 2.25em;
    &-prev{
      left: 0;
    }
    &-next{
      right: 0;
    }
    &.swiper-button-disabled{
      pointer-events: none;
    }
  }
  &__hidden{
    overflow: hidden;
  }
  &__slide{
    width: 100% !important;
  }
}


@media screen and (max-width: 992px){
  .adventurer{
    padding-top: 3.5em;
    &__desc{
      text-align: left;
      .body-l{
        font-size: 1.5em;
        line-height: 130%;
        @include grid100gap(1em);
      }
    }
    .advantages__items{
      margin-top: 1.125em;
    }
    .challenges-slider{
      &__nav{
        .h1{
          font-size: 1.5em;
        }
      }
    }
  }

  .main{
    padding: 0 0 3.5em;
  }

  .main-top{
    width: calc(100% + 3em);
    margin-left: -1.5em;
    padding: 18em 1.5em 4em;
    &__video{
      clip-path: unset;
    }
    &__name{
      font-size: 1em;
    }
    &__price{
      gap: .25em;
      margin-top: 2em;
      &-title{
        font-size: 1.25em;
        font-weight: 400;
      }
      &-num{
        font-size: 2em;
      }
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      font-size: 1em;
    }
  }

  .adventurer-slider{
    margin-top: 2em;
    .swiper-button{
      top: 1em;
    }
    &__slide{
      opacity: 0;
      &.swiper-slide-active{
        opacity: 1;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .adventurer{
    &__desc{
      .body-l{
        font-size: 1em;
      }
    }
    .advantages__items{
      margin-top: 2em;
    }
  }

  .main-top{
    width: calc(100% + 1em);
    margin-left: -.5em;
    padding: 18em .5em 4em;
    &__btn{
      width: 100%;
    }
  }
}
