.about-main{
  padding-top: 3.75em;
  background: #1B1D19;
  color: $white;
  &__inner{
    position: relative;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 3.75em 5em;
    z-index: 2;
  }
  &__img{
    @include imgCover(0);
    min-height: 100%;
    height: 41.25em;
    clip-path: polygon(0 0,100% 0,100% calc(100% - 5em),calc(100% - 2em) 100%,0 100%,0 0);
    object-fit: cover;
  }
  &__logo{
    width: 24em;
    display: block;
    margin-bottom: 1.25em;
  }
}

.about-info{
  margin-top: 6.25em;
  @include grid100gap(6.25em);
}

.about-card{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 1.5em 2.5em;
  &__img{
    img{
      width: 100%;
      display: block;
      clip-path: polygon(0 0,100% 0,100% calc(100% - 5em),calc(100% - 2em) 100%,0 100%,0 0);
    }
  }
  &__desc{
    margin-top: 1.25em;
    color: $white3;
  }
  @media screen and (min-width: 993px){
    &:nth-child(2){
      .about-card__img{
        order: 2;
      }
    }
  }
}

.history{
  overflow: hidden;
  background: #2F3629;
  color: $white;
  &__desc{
    color: $white3;
    text-align: center;
    width: 58em;
    margin: 1.75em auto 0;
  }
}

.history-slider{
  margin-top: 2.5em;
  .swiper-slide{
    width: 20%;
    padding-top: 5.375em;
    position: relative;
  }
  &__year{
    position: absolute;
    top: 0;
    left: 0;
    white-space: nowrap;
  }
}

.history-card{
  &__img{
    @include imgCover(74%);
  }
  &__content{
    position: relative;
    margin-top: 1.25em;
    padding-left: 1.25em;
    @include grid100gap(.75em);
    background-image: url(../img/rect.svg);
    background-repeat: no-repeat;
    background-position: left 0 top .25em;
    background-size: auto 1.25em;
  }
  &__year{
    font-size: 1.5em;
    line-height: 108%;
    font-weight: 600;
    letter-spacing: -.01em;
  }
  &__desc{
    font-size: 1em;
    line-height: 120%;
    letter-spacing: -.01em;
    color: $white3;
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding-top: 2.5em;
    &__content{
      position: static;
      padding: 0;
      width: 28em;
      margin: 0 auto 1.5em;
      text-align: center;
    }
    &__img{
      min-height: unset;
      height: 31.625em;
      clip-path: unset;
    }
    &__logo{
      width: 20.5em;
      margin: 0 auto 1em;
    }
    .h1{
      color: $white;
    }
  }

  .about-info{
    margin-top: 5.875em;
    gap: 3.375em;
  }

  .about-card{
    grid-template-columns: 100%;
    &__img{
      order: 2;
      img{
        height: 27em;
        object-fit: cover;
        clip-path: unset;
      }
    }
    &__desc{
      margin-top: 1.5em;
    }
  }

  .history{
    &__desc{
      width: 100%;
      margin-top: 1em;
      .body-l{
        font-size: 1.25em;
        letter-spacing: unset;
      }
    }
  }

  .history-slider{
    margin-top: 2em;
    .swiper-slide{
      width: 19.125em;
      padding-top: 4em;
    }
  }

  .history-card{
    &__content{
      margin-top: 1.5em;
      padding-left: 0;
      gap: .5em;
      background-position: left 0 top 0;
    }
    &__year{
      font-size: 1em;
      line-height: 125%;
      font-weight: 400;
      padding-left: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    padding-top: 2.5em;
    &__content{
      width: 100%;
    }
    &__img{
      height: 21.375em;
    }
  }

  .about-info{
    margin-top: 3.5em;
    gap: 2em;
  }

  .about-card{
    &__img{
      img{
        height: 18.125em;
      }
    }
    &__desc{
      margin-top: .75em;
      .body-l{
        font-size: 1em;
      }
    }
  }

  .history{
    &__desc{
      margin-top: 1.5em;
      text-align: left;
    }
  }

  .history-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
