.city-select{
  flex-grow: 1;
  position: relative;
  &._open{
    .city-select__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    cursor: pointer;
    height: 3.75em;
    background-color: $white2;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2em;
    &:after{
      content: '';
      width: 2.25em;
      height: 2.25em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
      transition: all .3s;
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #D9D6C7;
    background: $white2;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__item{
    label{
      display: flex;
      align-items: center;
      padding: .5em 2em;
      min-height: 3em;
      &:before{
        content: '';
        width: 1em;
        height: 1em;
        background-image: url(../img/marker.svg);
        @include bgImgSize(100%);
        margin-right: .25em;
      }
    }
    input:checked~label{
      background: #E1DECD;
    }
  }
}

.page-menu{
  padding: 3.75em 0 6.25em;
  &__inner{
    background: #2F3629;
    color: $white;
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
  &__btn{
    color: inherit;
    @include center;
    height: 8.5em;
    font-weight: 600;
    border-right: 1px solid #D9D6C7;
    &:last-child{
      border-right: none;
    }
    span{
      font-size: 1.25em;
      letter-spacing: -.01em;
    }
    img{
      width: 2.5em;
      margin-right: .5em;
    }
    &:hover{
      color: $white;
      opacity: .8;
    }
  }
}


@media screen and (max-width: 992px){
  .city-select{
    font-size: .875em;
    &__show{
      padding: 0 1em;
      height: 3.22em;
    }
    &__item{
      label{
        padding-left: 1em;
        padding-right: 1em;
      }
    }
  }

  .page-menu{
    &__inner{
      grid-template-columns: 50% 50%;
    }
    &__btn{
      height: 5.5em;
      padding: 0 1em;
      border-top: 1px solid #D9D6C7;
      &:first-child,
      &:nth-child(2){
        border-top: none;
      }
      span{
        font-size: 1em;
      }
      img{
        width: 2em;
        margin-right: .5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .page-menu{
    padding-bottom: 3.75em;
    &__btn{
      height: 4.8125em;
      flex-direction: column;
      align-items: flex-start;
      padding: 0 1em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.5em;
        margin: 0 0 .25em;
      }
    }
  }
}
