.choose2{
  background: #1B1D19;
  color: $white;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.625em));
    gap: 3.25em;
    margin-top: 4.25em;
  }
}

.choose-card{
  &__top{
    position: relative;
    margin-bottom: .875em;
  }
  &__img{
    @include imgCover(79%);
  }
  &__link{
    position: absolute;
    bottom: 1.875em;
    left: 1.875em;
    font-weight: 600;
    z-index: 2;
    text-transform: uppercase;
    min-width: 11em;
  }
}

.journey{
  background-color: #EEECE0;
  background-image: url(../img/journey.webp);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left bottom;
  &__tabs{
    margin-top: 1.75em;
    display: flex;
    justify-content: center;
    gap: 1.25em;
  }
  &__tab{
    background-color: #837E5F;
    color: $white;
    min-width: 18.8em;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
    &._active{
      background-color: $primary;
      pointer-events: none;
    }
  }
}

.journey-slider{
  margin-top: 2.75em;
  @media screen and (min-width: 577px){
    .swiper-container{
      width: 100%;
    }
    .swiper-wrapper{
      display: block;
      transform: unset;
    }
    .slider__nav{
      display: none;
    }
  }
}

.journey-card{
  .journey__tab{
    display: none;
  }
  &__top{
    text-align: center;
    width: 63em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__title{
    font-size: 1.5em;
    line-height: 108%;
    font-weight: 600;
    letter-spacing: -.01em;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .5em;
    font-size: 1.125em;
    line-height: 190%;
    letter-spacing: -.01em;
  }
  &__link{
    display: inline-block;
    margin: 0 auto;
    font-weight: 600;
    span{
      font-size: 1em;
      line-height: 287%;
      text-decoration: underline;
    }
  }
  &__video{
    margin-top: 1.25em;
    .video-card__img{
      height: 42em;
      padding: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .choose2{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
      margin-top: 2em;
    }
  }

  .choose-card{
    display: flex;
    flex-direction: column;
    &__top{
      position: relative;
      margin: 1em 0 0;
      order: 2;
    }
    &__img{
      @include imgCover(79%);
    }
    &__link{
      position: static;
      width: 100%;
      margin-top: 1em;
    }
    &__title{
      font-size: 1.5em;
    }
  }

  .journey{
    &__tabs{
      margin-top: 2em;
      gap: .75em;
    }
    &__tab{
      min-width: unset;
      width: calc(100% / 3 - .5em);
      padding: 0;
      text-align: center;
      span{
        font-size: 1em;
      }
    }
  }

  .journey-slider{
    margin-top: 1.5em;
  }

  .journey-card{
    &__top{
      text-align: center;
      width: 63em;
      max-width: 100%;
      margin: 0 auto;
    }
    &__title{
      font-size: 2em;
      width: 12.5em;
      margin: 0 auto;
      line-height: 118%;
      letter-spacing: unset;
      text-transform: unset;
    }
    &__desc{
      margin-top: .67em;
      font-size: 1.5em;
      line-height: 120%;
      letter-spacing: unset;
    }
    &__link{
      margin-top: 1.5em;
      font-weight: 400;
      span{
        font-size: 1.5em;
        line-height: 120%;
      }
    }
    &__video{
      margin-top: 2em;
      .video-card__img{
        height: 25em;
        &:after{
          width: 6.0625em;
          height: 6.0625em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .choose2{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .journey{
    &__tabs{
      display: none;
    }
    &__tab{
      width: 100%;
    }
    .tab-content{
      display: block;
    }
  }

  .journey-card{
    .journey__tab{
      display: flex;
      margin-bottom: 1.5em;
      background-color: $primary;
    }
    &__top{
      text-align: left;
    }
    &__title{
      font-size: 1.5em;
      text-align: center;
    }
    &__desc{
      margin-top: .75em;
      font-size: 1.25em;
    }
    &__link{
      margin: .75em 0 0;
      span{
        font-size: 1em;
      }
    }
    &__video{
      margin-top: 1.5em;
      .video-card__img{
        height: 18.125em;
      }
    }
  }
}
