.text{
  @include grid100gap(1em);
  h2,h3{
    font-size: 2.5em;
    line-height: 120%;
    letter-spacing: -.01em;
    text-transform: uppercase;
    font-weight: 600;
  }
  p{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: -.01em;
  }
  hr{
    display: block;
    margin: 0;
    height: .5em;
    opacity: 0;
  }
  img{
    width: 100%;
    display: block;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .text{
    gap: .25em;
    h2,h3{
      font-size: .875em;
      text-transform: unset;
    }
    p{
      font-size: .75em;
    }
    hr{
      height: .25em;
    }
  }
}


