.choose{
  background-color: #EEECE0;
  background-image: url(../img/choose.webp);
  background-size: 100% auto;
  background-position: left bottom;
  background-repeat: no-repeat;
  padding-bottom: 0;
  position: relative;
  &__img{
    position: absolute;
    width: 58.5em;
    right: 1.25em;
    bottom: 2.25em;
    mix-blend-mode: darken;
  }
}

.cars-slider{
  margin-top: 4.25em;
  .swiper-slide{
    width: 25%;
  }
}

.car-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 2.25em 1.25em 2em;
  background-color: #FEFDF6;
  clip-path: polygon(0 0,100% 0,100% calc(100% - 5em),calc(100% - 2em) 100%,0 100%,0 0);
  &__img{
    height: 10.3125em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 3em;
    @include grid100gap(.69em);
  }
  &__price{
    font-size: 1.5em;
    text-transform: uppercase;
  }
  &__props{
    @include grid100gap(.875em);
    margin-top: 1.75em;
  }
  &__prop{
    display: flex;
    justify-content: space-between;
    padding-bottom: .25em;
    border-bottom: 1px solid #1E201C;
    span{
      font-size: 1em;
      line-height: 150%;
      letter-spacing: -.01em;
    }
  }
  &__nav{
    padding-top: 3em;
    margin-top: auto;
    display: flex;
  }
  &__btn{
    padding: 0 2em;
    height: 2.875em;
    text-transform: uppercase;
    span{
      font-size: 1em;
    }
  }
}

.guarantee{
  margin-top: 8.75em;
  background: #EAE8DB;
  width: 48.375em;
  padding: 2.375em 2.5em 12.5em;
  &__top{
    @include grid100gap(.5em);
    margin-bottom: 4em;
  }
  &__desc{
    font-size: 1.5em;
    line-height: 130%;
    font-weight: 600;
    color: $primary;
    letter-spacing: -.01em;
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
    gap: 1.5em;
  }
  &__btn{
    min-width: 18em;
    padding: 0 1.5em;
    height: 2.875em;
    font-weight: 600;
    span{
      font-size: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .choose{
    overflow: hidden;
    padding-bottom: 16.125em;
    &__img{
      width: 29em;
      right: 2.25em;
    }
  }

  .cars-slider{
    margin-top: 1.5em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 24.5em;
    }
  }

  .guarantee{
    margin-top: 2em;
    background: transparent;
    width: 30.375em;
    max-width: 100%;
    padding: 0;
    &__top{
      gap: 1em;
      margin-bottom: 3em;
    }
    &__desc{
      font-weight: 400;
      letter-spacing: unset;
    }
    &__desc2{
      font-size: 1em;
      font-weight: 700;
      letter-spacing: unset;
    }
    &__nav{
      display: grid;
      grid-template-columns: 100%;
      gap: .75em;
      max-width: 23em ;
    }
    &__btn{
      min-width: unset;
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .choose{
    padding-bottom: 19.5em;
    &__img{
      bottom: 3.44em;
      right: -6em;
    }
  }

  .cars-slider{
    .swiper-slide{
      width: 100%;
    }
  }

  .guarantee{
    &__top{
      margin-bottom: .5em;
    }
    &__nav{
      max-width: 100%;
    }
  }
}
